@use '../../../../../../apps/sanare-app/src/styles/base/mixins' as *;

@use '@angular/material' as mat;

.favorite-programs-container {
  //   background-color: brown;
  height: 100%;

  display: block;
  overflow: hidden;
  padding: 16px;
  border-radius: 16px;

  .list-container {
    // margin-top: -8px;
    height: 86%;
    // max-height: calc(100vh - 226px);
    overflow-y: auto;
    // padding-bottom: 16px;

    // .scrollable-container {
    //   height: calc(100vh - 546px);
    //   overflow-y: scroll;

    .list-item-wrapper {
      display: flex;
      align-items: center;
      padding-right: 12px;
      margin-bottom: 4px;

      .list-title {
        white-space: wrap;
        margin: 0;
      }

      .template-icon {
        bottom: 4px;
        position: relative;
      }
    }
    // }
  }

  .programs-list-wrapper-placeholder {
    height: calc(100vh - 238px);
    overflow-y: hidden;

    .program-list-skeleton {
      border-radius: 8px;
      width: 100%;
      height: 78px;
      overflow: hidden;
      position: relative;
      margin-bottom: 4px;

      &::after {
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        transform: translateX(-100%);
        animation: shimmer 1s infinite;
        content: '';
      }

      @keyframes shimmer {
        100% {
          transform: translateX(100%);
        }
      }
    }
  }

  @include respond(compact) {
    .favorite-programs-container {
      padding: 8px;
    }
  }
}
